let app = require("@/config");
let enums = require("../enumsv1.js");
export default {
	onShow() {
		this.getData();
	},
	onLoad() {
	},
	methods: {
		getData() {
			uni.post("/api/workflow/MyFlow", {}, msg => {
				this.data = msg.data;
				//this.data = this.data.filter(e => e.State != 0);
			})
		},
		toExecute(item) {
			item.StepId=undefined;
			if(item.WorkflowName==="按团队申请实验室"){
				uni.setStorage({
					key : 'jmpInfo',
					data:item,
					success: () => {	//如果缓存成功则跳转
						uni.navigateTo({
							url: './roomFlowsCtrl'
						})
					}
				})
			}
			else if(item.WorkflowName==="按机位申请实验室"){
				uni.setStorage({
					key : 'jmpInfo',
					data:item,
					success: () => {	//如果缓存成功则跳转
						uni.navigateTo({
							url: './seatFlowsCtrl'
						})
					}
				})
			}
		}
	},
	data() {
		return {
			workflow: enums.workflow,
			wColor: enums.workflowColor,
			data: [],
			modalName: null,
			listTouchStart: 0,
			listTouchDirection: null
		}
	}
}
